import React from "react"
import { Link } from "gatsby"
import { Calendar, ArrowRight } from "@phosphor-icons/react"
import IconWrapper from "../icons/IconWrapper"

interface FeaturedPostCardProps {
  slug: string
  title: string
  date: string
  description?: string
  excerpt?: string
  tags?: string[]
  index?: number
}

function FeaturedPostCard({
  slug,
  title,
  date,
  description,
  excerpt,
  tags,
  index = 0,
}: FeaturedPostCardProps): React.ReactElement {
  const displayDescription = description || excerpt || ""

  return (
    <article
      className="group relative bg-background-card rounded-2xl overflow-hidden transition-all duration-300 hover:-translate-y-1 hover:shadow-lg shadow-sm fade-in-up"
      style={{ animationDelay: `${index * 100}ms` }}
    >
      {/* Subtle gradient overlay on hover */}
      <div className="absolute inset-0 bg-gradient-to-br from-accent/0 to-accent/0 group-hover:from-accent/[0.02] group-hover:to-accent/[0.05] transition-all duration-300" />

      <div className="relative p-5 sm:p-6 flex flex-col h-full">
        {/* Date */}
        <div className="flex items-center gap-1.5 text-foreground-muted text-xs mb-3">
          <IconWrapper icon={Calendar} size={14} className="text-accent" />
          <time>{date}</time>
        </div>

        {/* Title */}
        <h3 className="text-lg sm:text-xl font-semibold font-serif mb-2">
          <Link
            to={slug}
            className="text-foreground hover:text-accent transition-colors duration-200"
          >
            <span className="relative">
              {title}
              {/* Underline animation */}
              <span className="absolute -bottom-1 left-0 w-full h-0.5 bg-accent transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300 origin-left" />
            </span>
          </Link>
        </h3>

        {/* Description */}
        {displayDescription && (
          <p
            className="text-foreground-secondary text-[14px] line-clamp-3 mb-3 flex-grow"
            dangerouslySetInnerHTML={{ __html: displayDescription }}
          />
        )}

        {/* Footer: Tags + Read more */}
        <div className="flex items-center justify-between mt-auto pt-3">
          {/* Tags */}
          {tags && tags.length > 0 && (
            <div className="flex flex-wrap gap-1.5">
              {tags.slice(0, 3).map((tag) => (
                <Link
                  key={tag}
                  to={`/tags/${tag.toLowerCase().replace(/\s+/g, "-")}/`}
                  className="inline-flex items-center px-2 py-0.5 text-[11px] font-medium text-accent bg-accent-subtle/50 rounded hover:bg-accent hover:text-accent-foreground transition-colors duration-200"
                >
                  {tag}
                </Link>
              ))}
              {tags.length > 3 && (
                <span className="text-[11px] text-foreground-muted px-1">
                  +{tags.length - 3}
                </span>
              )}
            </div>
          )}

          {/* Read more link */}
          <Link
            to={slug}
            className="flex items-center gap-1 text-xs font-medium text-accent hover:text-accent-hover transition-colors duration-200 opacity-0 group-hover:opacity-100 transform translate-x-2 group-hover:translate-x-0 transition-all duration-300"
          >
            阅读更多
            <IconWrapper icon={ArrowRight} size={14} />
          </Link>
        </div>
      </div>
    </article>
  )
}

export default FeaturedPostCard
