import { useState, useEffect } from "react"

/**
 * Hook to track which section is currently in view
 * Used for table of contents active state highlighting
 *
 * @param selector - CSS selector for the headings to track (e.g., "h2, h3")
 * @param options - IntersectionObserver options
 * @returns The id of the currently active section
 */
export function useScrollSpy(
  selector: string,
  options: IntersectionObserverInit = {}
): string | null {
  const [activeId, setActiveId] = useState<string | null>(null)

  useEffect(() => {
    if (typeof document === "undefined") return

    const elements = document.querySelectorAll(selector)
    if (elements.length === 0) return

    // Map to store visibility ratios
    const visibilityMap = new Map<string, number>()

    const observerOptions: IntersectionObserverInit = {
      root: null,
      rootMargin: "-80px 0px -60% 0px", // Offset for sticky header and prefer top sections
      threshold: [0, 0.25, 0.5, 0.75, 1],
      ...options,
    }

    const observer = new IntersectionObserver((entries) => {
      // Update visibility ratios
      entries.forEach((entry) => {
        const id = entry.target.id
        if (id) {
          visibilityMap.set(id, entry.intersectionRatio)
        }
      })

      // Find the element with highest visibility ratio
      let maxRatio = 0
      let maxId: string | null = null

      visibilityMap.forEach((ratio, id) => {
        if (ratio > maxRatio) {
          maxRatio = ratio
          maxId = id
        }
      })

      // Only update if we have a visible element
      if (maxId && maxRatio > 0) {
        setActiveId(maxId)
      }
    }, observerOptions)

    // Observe all elements
    elements.forEach((element) => {
      observer.observe(element)
      // Initialize visibility map
      if (element.id) {
        visibilityMap.set(element.id, 0)
      }
    })

    return () => {
      observer.disconnect()
    }
  }, [selector, JSON.stringify(options)])

  return activeId
}
