import { useEffect, useCallback } from "react"

interface UseCodeCopyOptions {
  selector?: string
  timeout?: number
}

export function useCodeCopy(options: UseCodeCopyOptions = {}) {
  const { selector = ".gatsby-highlight", timeout = 2000 } = options

  const copyCode = useCallback(
    async (button: HTMLButtonElement, codeBlock: HTMLElement) => {
      const code = codeBlock.textContent || ""

      try {
        await navigator.clipboard.writeText(code)

        // Update button state to show success
        button.classList.add("copied")
        button.setAttribute("aria-label", "已复制")

        // Reset button after timeout
        setTimeout(() => {
          button.classList.remove("copied")
          button.setAttribute("aria-label", "复制代码")
        }, timeout)
      } catch (err) {
        console.error("Failed to copy code:", err)
        button.setAttribute("aria-label", "复制失败")

        setTimeout(() => {
          button.setAttribute("aria-label", "复制代码")
        }, timeout)
      }
    },
    [timeout]
  )

  useEffect(() => {
    if (typeof document === "undefined") return

    // Find all code blocks
    const codeBlocks = document.querySelectorAll(selector)

    codeBlocks.forEach((block) => {
      // Skip if already has a copy button
      if (block.querySelector(".code-copy-button")) return

      const highlightBlock = block as HTMLElement

      // Create copy button
      const copyButton = document.createElement("button")
      copyButton.className = "code-copy-button"
      copyButton.setAttribute("aria-label", "复制代码")
      copyButton.setAttribute("title", "复制代码")
      copyButton.type = "button"

      // Copy icon SVG
      copyButton.innerHTML = `
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <rect x="9" y="9" width="13" height="13" rx="2" ry="2"></rect>
          <path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"></path>
        </svg>
      `

      // Check icon for copied state
      const checkIcon = `
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <polyline points="20 6 9 17 4 12"></polyline>
        </svg>
      `

      // Find the code element
      const codeElement = highlightBlock.querySelector("pre") as HTMLElement
      if (!codeElement) return

      // Add click handler
      copyButton.addEventListener("click", async () => {
        await copyCode(copyButton, codeElement)

        // Temporarily change icon to checkmark
        const originalIcon = copyButton.innerHTML
        copyButton.innerHTML = checkIcon

        setTimeout(() => {
          if (!copyButton.classList.contains("copied")) {
            copyButton.innerHTML = originalIcon
          } else {
            // Keep checkmark until copied state is removed
            const resetIcon = () => {
              copyButton.innerHTML = originalIcon
              copyButton.removeEventListener("transitionend", resetIcon)
            }
            copyButton.addEventListener("transitionend", resetIcon)
          }
        }, timeout)
      })

      // Append button to code block
      highlightBlock.appendChild(copyButton)
    })

    // Cleanup function
    return () => {
      codeBlocks.forEach((block) => {
        const button = block.querySelector(".code-copy-button")
        if (button) {
          button.remove()
        }
      })
    }
  }, [selector, copyCode])
}

export default useCodeCopy
