import React from "react"
import { Link } from "gatsby"
import { Calendar, Clock } from "@phosphor-icons/react"
import IconWrapper from "../icons/IconWrapper"

interface ArticleListItemProps {
  slug: string
  title: string
  date: string
  description?: string
  excerpt?: string
  tags?: string[]
  index?: number
}

function ArticleListItem({
  slug,
  title,
  date,
  description,
  excerpt,
  tags,
  index = 0,
}: ArticleListItemProps): React.ReactElement {
  const displayDescription = description || excerpt || ""

  // Calculate reading time (rough estimate: 200 words per minute)
  const wordCount = displayDescription?.split(/\s+/).length || 0
  const readingTime = Math.max(1, Math.ceil(wordCount / 200))

  return (
    <article
      className="group py-4 fade-in-up"
      style={{ animationDelay: `${index * 80}ms` }}
    >
      <div className="flex flex-col sm:flex-row sm:items-start sm:justify-between gap-2">
        {/* Main content */}
        <div className="flex-1 min-w-0">
          {/* Title with animated underline */}
          <h3 className="text-base sm:text-lg font-medium mb-1.5">
            <Link
              to={slug}
              className="text-foreground hover:text-accent transition-colors duration-200"
            >
              <span className="relative inline-block">
                {title}
                {/* Underline animation - expands from left to right */}
                <span className="absolute -bottom-0.5 left-0 w-full h-0.5 bg-accent transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300 origin-left" />
              </span>
            </Link>
          </h3>

          {/* Description */}
          {displayDescription && (
            <p
              className="text-foreground-secondary text-[14px] line-clamp-2 mb-2"
              dangerouslySetInnerHTML={{ __html: displayDescription }}
            />
          )}

          {/* Meta info and tags row */}
          <div className="flex flex-wrap items-center gap-3">
            {/* Date */}
            <div className="flex items-center gap-1 text-foreground-secondary text-xs">
              <IconWrapper icon={Calendar} size={12} />
              <time>{date}</time>
            </div>

            {/* Reading time */}
            <div className="flex items-center gap-1 text-foreground-secondary text-xs">
              <IconWrapper icon={Clock} size={12} />
              <span>{readingTime} 分钟</span>
            </div>

            {/* Tags */}
            {tags && tags.length > 0 && (
              <div className="hidden sm:flex items-center gap-1.5 ml-auto">
                {tags.slice(0, 2).map((tag) => (
                  <Link
                    key={tag}
                    to={`/tags/${tag.toLowerCase().replace(/\s+/g, "-")}/`}
                    className="inline-flex items-center px-1.5 py-0.5 text-[11px] font-medium text-foreground-secondary bg-background-secondary rounded hover:bg-accent hover:text-accent-foreground transition-colors duration-200"
                  >
                    {tag}
                  </Link>
                ))}
                {tags.length > 2 && (
                  <span className="text-[11px] text-foreground-secondary">
                    +{tags.length - 2}
                  </span>
                )}
              </div>
            )}
          </div>

          {/* Mobile tags - shown below meta on small screens */}
          {tags && tags.length > 0 && (
            <div className="flex sm:hidden flex-wrap items-center gap-1.5 mt-2">
              {tags.map((tag) => (
                <Link
                  key={tag}
                  to={`/tags/${tag.toLowerCase().replace(/\s+/g, "-")}/`}
                  className="inline-flex items-center px-1.5 py-0.5 text-[11px] font-medium text-foreground-secondary bg-background-secondary rounded hover:bg-accent hover:text-accent-foreground transition-colors duration-200"
                >
                  {tag}
                </Link>
              ))}
            </div>
          )}
        </div>
      </div>
    </article>
  )
}

export default ArticleListItem
