import React, { useState, useEffect } from "react"
import { List, ChevronRight, ChevronLeft } from "lucide-react"
import { useScrollSpy } from "../../hooks"

interface TOCItem {
  id: string
  text: string
  level: number
}

interface TableOfContentsProps {
  /** HTML string from Gatsby's tableOfContents (contains nested <ul> structure) */
  html: string
  /** Additional CSS class names */
  className?: string
  /** Title text (default: "目录") */
  title?: string
}

/**
 * Parse Gatsby's tableOfContents HTML into structured data
 */
function parseTOCItems(html: string): TOCItem[] {
  if (typeof document === "undefined") return []

  const parser = new DOMParser()
  const doc = parser.parseFromString(html, "text/html")
  const items: TOCItem[] = []

  // Find all anchor tags in the TOC
  const links = doc.querySelectorAll("a")
  links.forEach((link) => {
    const href = link.getAttribute("href")
    if (href && href.startsWith("#")) {
      // Decode the ID from href (handles URL-encoded characters like %20 for spaces)
      const rawId = href.slice(1) // Remove the # prefix
      const id = decodeURIComponent(rawId)
      const text = link.textContent?.trim() || ""

      // Calculate level based on nesting depth
      let level = 1
      let parent = link.parentElement
      while (parent) {
        if (parent.tagName === "UL") {
          // Check if this UL is inside an LI (nested list)
          if (parent.parentElement?.tagName === "LI") {
            level++
          }
        }
        parent = parent.parentElement
      }

      items.push({ id, text, level: Math.min(level, 6) })
    }
  })

  return items
}

/**
 * TableOfContents Component
 *
 * Displays a sticky table of contents for blog posts with:
 * - Active section highlighting based on scroll position
 * - Smooth scrolling to sections
 * - Theme-aware styling
 * - Responsive design (hidden on mobile)
 */
export const TableOfContents: React.FC<TableOfContentsProps> = ({
  html,
  className = "",
  title = "目录",
}) => {
  // Use state to ensure TOC is parsed on client side after hydration
  const [items, setItems] = useState<TOCItem[]>([])
  const [mounted, setMounted] = useState(false)
  const [isCollapsed, setIsCollapsed] = useState(true) // Default collapsed

  useEffect(() => {
    setMounted(true)
    setItems(parseTOCItems(html))
    // Load saved preference from localStorage
    if (typeof window !== "undefined") {
      const saved = localStorage.getItem("toc-collapsed")
      if (saved !== null) {
        setIsCollapsed(saved === "true")
      }
    }
  }, [html])

  const toggleCollapsed = () => {
    const newState = !isCollapsed
    setIsCollapsed(newState)
    if (typeof window !== "undefined") {
      localStorage.setItem("toc-collapsed", String(newState))
    }
  }

  // Track active section based on heading visibility - support all heading levels
  const activeId = useScrollSpy("h1[id], h2[id], h3[id], h4[id], h5[id], h6[id]")

  // Handle smooth scroll to section
  const handleClick = (e: React.MouseEvent<HTMLAnchorElement>, id: string) => {
    e.preventDefault()
    e.stopPropagation()

    // Try multiple strategies to find the element
    // 1. Try the original ID
    // 2. Try URL-decoded ID (in case ID contains encoded characters like %20)
    // 3. Try URL-encoded ID (in case ID needs encoding)
    const decodedId = decodeURIComponent(id)
    const encodedId = encodeURIComponent(id)

    let element =
      document.getElementById(id) ||
      document.getElementById(decodedId) ||
      document.getElementById(encodedId)

    // If still not found, try to find by data attribute or query all headings
    if (!element) {
      const headings = document.querySelectorAll(
        "h1[id], h2[id], h3[id], h4[id], h5[id], h6[id]"
      )
      headings.forEach((heading) => {
        const headingId = heading.id
        if (
          headingId === id ||
          headingId === decodedId ||
          headingId === encodedId ||
          decodeURIComponent(headingId) === decodedId
        ) {
          element = heading as HTMLElement
        }
      })
    }

    if (element) {
      const headerOffset = 80 // Account for sticky header
      const elementPosition = element.getBoundingClientRect().top
      const offsetPosition = elementPosition + window.scrollY - headerOffset

      // Use both methods for better browser compatibility
      window.scrollTo({
        top: offsetPosition,
        behavior: "smooth",
      })

      // Update URL hash without jumping - use the element's actual ID
      if (window.history && window.history.pushState) {
        window.history.pushState(null, "", `#${element.id}`)
      }
    } else {
      console.warn(`[TOC] Could not find element with id: ${id}`)
    }
  }

  // Don't render during SSR or if no items
  if (!mounted || items.length === 0) {
    return null
  }

  return (
    <aside
      className={`toc-sidebar hidden xl:block ${className} ${
        isCollapsed ? "toc-collapsed" : ""
      }`}
      aria-label="Table of contents"
    >
      <div className="toc-container">
        {/* Header with toggle button */}
        <div className="toc-header">
          <List className="w-4 h-4 text-primary" />
          {!isCollapsed && <span className="toc-title">{title}</span>}
          <button
            onClick={toggleCollapsed}
            className="toc-toggle-btn"
            aria-label={isCollapsed ? "展开目录" : "收起目录"}
            aria-expanded={!isCollapsed}
            title={isCollapsed ? "展开目录" : "收起目录"}
          >
            {isCollapsed ? (
              <ChevronLeft className="w-4 h-4" />
            ) : (
              <ChevronRight className="w-4 h-4" />
            )}
          </button>
        </div>

        {/* TOC List - hidden when collapsed */}
        {!isCollapsed && (
          <nav className="toc-nav">
            <ul className="toc-list">
              {items.map((item) => (
                <li
                  key={item.id}
                  className={`toc-item toc-level-${item.level} ${
                    activeId === item.id ? "toc-item-active" : ""
                  }`}
                >
                  <a
                    href={`#${item.id}`}
                    onClick={(e) => handleClick(e, item.id)}
                    className="toc-link"
                    aria-current={activeId === item.id ? "location" : undefined}
                    style={{ cursor: "pointer" }}
                  >
                    <span className="toc-indicator" />
                    <span className="toc-text">{item.text}</span>
                  </a>
                </li>
              ))}
            </ul>
          </nav>
        )}
      </div>
    </aside>
  )
}
